<template>
  <el-container style="background-color:rgba(209, 233, 255, 0.2);height: 100%;">
    <el-header class="header">
      <el-row type="flex" justify="end" align="middle">
        <el-col :span="22">
          <el-menu :default-active="activeIndex" mode="horizontal" @select='handleSelect'>
            <el-menu-item index="1">图片</el-menu-item>
            <el-menu-item index="2">AI绘画教程</el-menu-item>
            <el-menu-item index="3">编程教程</el-menu-item>
            <el-menu-item index="4">关于</el-menu-item>
          </el-menu>
        </el-col>
        <el-col :span="2">
          <div style="width: 100%;">
            <el-button style="float:right" type="primary" >登录</el-button>
          </div>
        </el-col>
      </el-row>
    </el-header>
    <el-main style="margin: 0px 30px;padding: 5px;height:100%;overflow: hidden;"  v-if="activeIndex === '1'">
      <div class="scroll" style="height:100%;overflow-y: scroll;" @scroll="onScroll($event)">
        <el-row type="flex" justify="center" >
          <div class="image_list_list">
            <div class="image_list" v-for=" imgs in imgListList" :key="imgs.name" >
              <div class="image_item" v-for="img in imgs.list" :key="img.name">
                <el-row type="flex" justify="center">
                  <el-image
                    style="border-radius: 3px;"
                    :src="img.url"
                    fit="scale-down"
                    :preview-src-list="imgss"
                    lazy
                    >
                  </el-image>
                  <div class="image-download" @click="download(img.url)" v-bind:title="downloadTitle">
                    <i class="el-icon-download"></i>
                  </div>
                </el-row>
              </div>
            </div>
          </div>
        </el-row>
      <el-row style="margin-top: 50px;" ><div style="cursor: pointer;"  @click="getPicture(true)"><el-divider>加载更多</el-divider></div></el-row>
    </div>
    </el-main>
    <el-main style="margin: 0px 30px;" v-else-if="activeIndex === '2'">
      <el-row type="flex" justify="center">AI绘画教程</el-row>
    </el-main>
    <el-main style="margin: 0px 30px;" v-else-if="activeIndex === '3'">
      <el-row type="flex" justify="center">编程教程</el-row>
    </el-main>
    <el-main style="margin: 0px 30px;" v-else-if="activeIndex === '4'">
      <el-row type="flex" justify="center">关于界面</el-row>
    </el-main>
  </el-container>
</template>

<script>
import { getPicturePageAndLimit, getPictureFile } from '../api/picture.js'
export default {
  data () {
    return {
      input: '',
      form: {
        user: '',
        password: ''
      },
      imgListList: [],
      imgss: [],
      downloadTitle: '下载',
      activeIndex: '1',
      isloading: false,
      imgListNum: 3,
      page: 1,
      limit: 8
    }
  },
  methods: {
    onSubmit () {
      console.log('登录')
    },
    onReset () {
      console.log('重置')
    },
    handleSelect (key, keyPath) {
      this.activeIndex = key
    },
    download (index) {
      getPictureFile(index).then((response) => {
        const blob = new Blob([response.data])
        const elink = document.createElement('a')
        elink.download = index.substring(index.lastIndexOf('/'))
        elink.style.display = 'none'
        elink.href = URL.createObjectURL(blob)
        document.body.appendChild(elink)
        elink.click()
        URL.revokeObjectURL(elink.href) // 释放URL 对象
        document.body.removeChild(elink)
      }).catch((error) => {
        this.$message({
          message: error
        })
      })
    },
    onScroll (event) {
      if ((event.target.scrollHeight - event.target.clientHeight) * 0.9 < event.target.scrollTop) {
        if (!this.isloading) {
          this.getPicture()
        }
      }
      if ((event.target.scrollHeight - event.target.clientHeight) * 0.6 > event.target.scrollTop) {
        this.isloading = false
      }
    },
    getPicture (isForce) {
      if (!this.isloading || isForce) {
        this.isloading = true
        return getPicturePageAndLimit({'page': this.page, 'limit': this.limit}).then((response) => {
          var data = response.data
          console.log(data)
          var i = 1
          for (const iterator of data) {
            if (i % this.imgListNum !== 0) {
              iterator.name = this.page * i + i - 1
              this.imgListList[(i % this.imgListNum) - 1].list.push(iterator)
            } else {
              this.imgListList[this.imgListNum - 1].list.push(iterator)
            }
            this.imgss.push(iterator.url)
            i++
          }
          this.isloading = false
          this.page++
        })
      }
    }
  },
  created () {

  },
  mounted () {
    for (var i = 0; i < this.imgListNum; i++) {
      this.imgListList.push({name: 'imgListNum' + i, list: []})
    }
    // this.getPicture()
    // this.getPicture()
    this.getPicture()
      .catch(() => {
        for (var i = 1; i <= 300; i++) {
          if (i % this.imgListNum !== 0) {
            this.imgListList[(i % this.imgListNum) - 1].list.push({url: require('../img/' + 7 + '.png'), name: i.toFixed() + '.png'})
          } else {
            this.imgListList[this.imgListNum - 1].list.push({url: require('../img/' + 7 + '.png'), name: i.toFixed() + '.png'})
          }
          this.imgss.push(require('../img/' + 7 + '.png'))
        }
        console.log(this.imgListList)
        this.isloading = false
      })
  }
}
</script>

<style scoped>

.toolbox{
  display: inline-block;
  display: inline-flex;
  justify-content: center;
}
.header_pBtn {
  display: inline-block;
  padding: 5px 15px;
  margin: 10px;
  border-radius: 15px;
  font-size:medium;
  cursor:pointer;
  height: fit-content;
  width: fit-content;
}
.image_list_list{
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-around;
  justify-content: flex-start;
  height: 100%;
  width: 100%;
}
.image_list{
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  align-content: center;
  width: 33%;
  height:100%;
  margin: 0.1%;
}
.image_item{
  height: 100%;
  width: 100%;
  margin: 0.3%;
}
.header_pBtn:hover{
  background:rgba(180, 203, 219, 0.232);
}
.image-slot{
  height: 100%;
  width: 100%;
}
.image-download{
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 5px;
  border-radius: 2px;
  width: 15px;
  height: 15px;
  position: absolute;
  right: 2%;
  bottom: 2%;
  background-color: rgba(210, 220, 229, 0.412);
}
.image-download:hover{
  background-color: aliceblue;
}
.header{
  padding: 0px 50px;
  background-color:rgba(255, 255, 255, 1);
  border-bottom: solid 1px #5a606e33;
}
.scroll::-webkit-scrollbar {
  width: 4px;
  height: 4px;
}
.scroll::-webkit-scrollbar-track {
  background-color: transparent;
  -webkit-border-radius: 1em;
  -moz-border-radius: 1em;
  border-radius: 1em;
}
.scroll::-webkit-scrollbar-thumb {
  background-color: rgba(148, 180, 244, 0.27);
  -webkit-border-radius: 1em;
  -moz-border-radius: 1em;
  border-radius: 1em;
}
</style>



